<!-- 컴포넌트 ui 정의 -->
<!-- root element가 하나만 있어야함 -->
<template>
  <v-card>
    <v-card-title>Login</v-card-title>
      <v-text-field
        v-model="user.id"
        label="Id"
        type="id"
        prepend-icon="mdi-email"
        autofocus>
      </v-text-field>
      <v-text-field
        v-model="user.password"
        label="Password"
        type="password"
        prepend-icon="mdi-lock">
      </v-text-field>
      <v-checkbox
        v-model="ex4"
        label="로그인 상태유지"
        color="blue"
        value="red"
        hide-details>
      </v-checkbox>
      <v-layout justify-center>
        <v-card-actions>
          <v-btn block color="primary"
            @click="login()">
            로그인
          </v-btn>
        </v-card-actions>
        <v-card-actions>
          <v-btn block color="primary"
            @click="gohome()">
            취소
          </v-btn>
        </v-card-actions>
      </v-layout>
      
    <!-- 기본적으로 버튼과 버튼 사이에 마진이 들어감 -->
  </v-card>
</template>

<script>
// import axios from 'axios'
import auth from "@/apis/auth";
export default {
  // 컴포넌트의 대표이름(devtools에 나오는 이름)
  name: "Login",
  // 추가하고 싶은 컴포넌트를 등록
  components: {
    
  },
  //컴포넌트에서 사용하는 데이터를 정의
  // return 값이 되려면 괄호로 감싸야함
  data() {
    return {
      user : {
        id: '',
        password: ''
      }
    };
  },
  //컴포넌트에서 사용하는 메소드 정의
  methods: {
    gohome(){
      this.$router.push('/');
    },
    async login(){
      try{
        console.log(this.user);
        const response = await auth.login(this.user);
        console.log(response);
        this.$store.dispatch('saveAuth', {
          userId : response.data.mid,
          authToken : response.data.jwt
          }
        );
        this.$router.push('/');
      }
      catch(e){
        try{
          console.log(e);
          if(e.response.status == 401){
            alert('로그인 정보가 올바르지 않습니다.');
          }
          if(e.response.status==500){
            alert('서버 오류 입니다');
          }
        }catch(e){
          console.log(e);
          alert("네트워크 에러로 로그인이 실패하였습니다");
        }
      }
    }
  },
};
</script>
<!-- 컴포넌트 -->
<style scoped></style>
